module.exports = ({ el, services }) => () => {

    const $chooseImages = el('a', 'choose-images', {
        textContent: 'Choose images'
    }).addEventListener('click', e => {
        e.preventDefault();
        $fileInput.click();
    });

    const $fileInput = el('input', {
        type: 'file',
        multiple: true,
        accept: 'image/*'
    }).addEventListener('change', () => {
        services.tags.insertFileBatchAsync($fileInput.files);
    });

    return el('span').append($chooseImages, $fileInput);

};
