const Picker = require('vanilla-picker').default || require('vanilla-picker');

module.exports = ({ el, services }) => roleId => {

    const role = services.roles.getRole(roleId);

    const $colourPicker = el('div', 'colour-picker');

    new Picker({
        parent: $colourPicker,
        color: role.colour,
        onChange: e => services.roles.changeRoleColour(roleId, e.hex)
    });

    return $colourPicker;

};
