const debounce = require('lodash/debounce');

module.exports = ({ el, tagList, services, subscriptions, config }) => () => {
    
    const $$tags = {};

    const $tags = el('div', { className: 'tags' });

    const insertTag = instanceId => {
        const $tag = tagList.tag(instanceId);
        $$tags[instanceId] = $tag;
        $tags.append($tag);
    };

    const removeTag = instanceId => {
        $$tags[instanceId].remove();
        delete $$tags[instanceId];
    };

    subscriptions.tagInstances.onInsert(instanceId => {
        insertTag(instanceId);
        delayedSort();
    });

    subscriptions.tagInstances.onBeforeRemove(removeTag);

    const sort = () => {
        services.tags.sortInstances().forEach(instance => {
            $tags.appendChild($tags.removeChild($$tags[instance.id]));
        });
    };

    const delayedSort = debounce(sort, config.debounce.sortTagList);

    subscriptions.settings.options.onChange('sort', sort).invoke();
    subscriptions.tagInstances.onChangeAny('name', delayedSort);
    subscriptions.tagInstances.onChangeAny('roleName', delayedSort);

    return $tags;
    
};
