module.exports = ({ el, subscriptions, config }) => () => {

    const $style = el('style');

    subscriptions.settings.options.onChange('size', size => {
        const width = size - (config.tags.imagePadding * 2);
        $style.textContent = `
            .tags { grid-template-columns: repeat(auto-fill, ${size}px); }
            .tag-image-container { width: ${width}px; height: ${width}px; }
        `;
    }).invoke();

    return $style;
    
};

/*
Absolute size needed for `.tag-image-container` because the image is loaded as a 
CSS `background-image` of a <div> instead of an <img> for performance reasons.
*/
