const storeNames = ['settings', 'roles', 'tags', 'tagInstances'];
const settingsNames = ['app', 'options', 'gravatar'];

module.exports = ({ storage }) => state => {

    const stores = storage.stores({ state, storeNames });
    const settings = storage.settings({ stores, settingsNames });
    const subscriptions = storage.subscriptions({ stores, settings });

    return { stores, settings, subscriptions };

};