module.exports = ({ el, app, subscriptions }) => () => {

    const $$modals = Object.values(app.modals).map(modal => modal());

    const $container = el('div').append(
        app.styles(), 
        app.header(), 
        app.dropzone().append(
            app.controlPanel(), 
            app.tagList()
        ),
        ...$$modals
    );

    subscriptions.settings.app.onChange('modal', modal => {
        $container.className = `app modal-${Boolean(modal)}`;
    }).invoke();

    return $container;

};
