module.exports = ({ el, app, services, config }) => () => {

    const $heading = el('h1', 'welcome-title', {
        textContent: `Welcome to ${config.app.name}`
    });

    const $image = el('img', {
        src: 'img/welcome.png',
        width: 800
    });

    const $continue = el('button', { textContent: 'Continue' });
    $continue.addEventListener('click', services.settings.clearModal);
    
    const $actions = el('div', 'actions').append($continue);
    const $content = el('div', 'welcome').append($heading, $image, $actions);

    return app.modal({
        name: 'welcome', 
        content: $content
    });
    
};
