module.exports = ({ elements, services, subscriptions }) => roleId => {

    const $roleBadge = elements.editableSpan(e => {
        const name = e.target.textContent;
        services.roles.changeRoleName(roleId, name);
    });
    
    $roleBadge.className = `role-badge role${roleId}`;

    subscriptions.roles.onChange(roleId, 'name', name => {
        $roleBadge.textContent = name;
    }).invoke();

    return $roleBadge;

};
