module.exports = ({ elements }) => onChange => {

    const $span = elements.el('span')
        .addEventListener('blur', onChange)
        .addEventListener('keydown', keypress(onChange));
    
    $span.setAttribute('contenteditable', true);
    
    return $span;
};

const keypress = callback => e => {
    if (e.code !== 'Enter') return;
    callback(e);
    e.preventDefault();
};

/*
Content editable span preferred over text field for the ability to expand/contract while editing.
*/
