module.exports = ({ el, styles, subscriptions }) => () => {

    const $styles = el('div').append(
        styles.styles.tagShape(),
        styles.styles.tagSize(),
        styles.styles.tagSpacing(),
        styles.styles.tagOutline()
    );

    subscriptions.roles.onInsert(roleId => $styles.append(styles.styles.role(roleId)));
    subscriptions.tags.onInsert(tagId => $styles.append(styles.styles.tagImage(tagId)));

    return $styles;

};
