module.exports = ({ elements, services, subscriptions }) => instanceId => {

    const $name = elements.editableSpan(e => {
        const name = e.target.textContent.trim();
        const { tagId } = services.tags.getInstance(instanceId);
        services.tags.changeTagName(tagId, name);
    });

    $name.className = 'tag-name';

    subscriptions.tagInstances.onChange(instanceId, 'name', name => {
        $name.textContent = name;
    }).invoke();

    return $name;

};
