module.exports = ({ el, roleList, subscriptions }) => () => {

    const $$roles = {};

    const $roles = el('div', 'role-list visible-false');

    subscriptions.roles.onInsert(roleId => {
        const $role = roleList.roleCustomiser(roleId);
        $$roles[roleId] = $role;
        $roles.appendChild($role);
    });

    subscriptions.tags.onFirstInsert(() => {
        $roles.className = 'role-list visible-true';
    });

    return $roles;
    
};
