module.exports = ({ el, roleCustomiser, services }) => roleId => {
    
    const isNilRole = services.roles.isNilRole(roleId);

    const $masterRoleBadge = roleCustomiser.masterRoleBadge(roleId);
    const $colourPicker = roleCustomiser.roleColourPicker(roleId);

    return el('span', `role-customiser role${roleId} nil-role-${isNilRole}`)
        .append($masterRoleBadge, $colourPicker);

};
