const initialise = require('./initialise');

module.exports = window => {    

    const { components, services, config, context } = initialise({ window });    
    window.agileavatars = context;
    document.title = config.app.name;
    document.head.append(components.analytics.google(window));
    document.body.append(components.app());
    services.settings.changeModal('welcome');

};
