module.exports = ({ elements }) => ({ handleFiles, dropEventDataOverride }) => {

    const drop = event => {
        const e = dropEventDataOverride || event;
        handleFiles(e.dataTransfer.files);
        return silence(e);
    };
    
    return elements.el('div', { className: 'dropzone' })
        .addEventListener('dragenter', silence, false)
        .addEventListener('dragover', silence, false)
        .addEventListener('drop', drop, false);
        
};

const silence = e => {
    e.stopPropagation();
    e.preventDefault();
    return false;
};

