module.exports = ({ elements, services, subscriptions }) => instanceId => {

    const $role = elements.editableSpan(e => {
        const name = e.target.textContent.trim();
        services.tags.changeInstanceRole(instanceId, name);
    });

    $role.className = 'role-badge';

    subscriptions.tagInstances.onChange(instanceId, 'roleName', roleName => {
        $role.textContent = roleName;
    }).invoke();

    return $role;

};
