module.exports = ({ io }) => async tagDataPromise => {

    const tagData = await tagDataPromise;
    const { imageBlob } = await tagData;
        
    if (!imageBlob) return tagData;
    if (!imageBlob.type.startsWith('image/')) {
        // TODO: find out why this happens in tests.
        return tagData;
    }

    const image = await new Promise(resolve => {
        const reader = io.createFileReader();
        reader.addEventListener('load', () => resolve(reader.result));
        reader.readAsDataURL(imageBlob);
    });

    return { ...tagData, image };
    
};
