const status = {
    ready: 'ready',
    working: 'working',
    error: 'error'
};

module.exports = ({ settings }) => {
    const statusIs = status => () => settings.gravatar.getState().status === status;

    const isReady = statusIs(status.ready);
    const isWorking = statusIs(status.working);
    const isError = statusIs(status.error);

    const ready = () => {
        settings.gravatar.setState({
            status: status.ready,
            freetext: '',
            errorMessage: ''
        });
    };

    const resume = () => {
        settings.gravatar.setState({
            status: status.ready,
            errorMessage: ''
        });
    };

    const working = () => {
        settings.gravatar.setState({ status: status.working });
    };

    const error = errorMessage => {
        settings.gravatar.setState({ status: status.error, errorMessage });
    };

    return { isReady, isWorking, isError, ready, resume, working, error };
};
