module.exports = ({ services, stores, subscriptions }) => async tagData => {

    const tag = await services.tags.buildTagAsync(tagData);

    return stores.tags.insert(tag, tagId => {
        subscriptions.tags.onChange(tagId, 'name', services.tags.setupTagPropagation(tagId)).invoke();
        subscriptions.tags.onChange(tagId, 'roleId', services.tags.setupRolePropagation(tagId)).invoke();
    });
    
};
