module.exports = ({ elements, services, subscriptions }) => ({ name, content, title }) => {

    const dismiss = services.settings.clearModal;

    const onModalChange = listener => {
        subscriptions.settings.app.onChange('modal', modal => listener(modal)).invoke();
    };

    return elements.modal({        
        name,
        content,
        title,
        dismiss,
        onModalChange
    });

};
