module.exports = ({ el, subscriptions }) => roleId => {

    const $style = el('style');

    subscriptions.roles.onChange(roleId, 'colour', colour => {
        $style.textContent = `
            .role${roleId} .tag-image { border-color: ${colour}; }            
            .role${roleId} .role-badge { background-color: ${colour}; }
        `;
    }).invoke();

    return $style;

};
