module.exports = ({ el, services, subscriptions }) => shapeName => {
    
    const $shape = el('span', `shape-option ${shapeName}`, {
        title: `Change shape to ${shapeName}`
    }).addEventListener('click', () => {
        services.settings.changeOption('shape', shapeName);
    });

    subscriptions.settings.options.onChange('shape', selectedShape => {
        $shape.classList.toggle('selected', shapeName === selectedShape);
    }).invoke();

    return $shape;
    
};
