module.exports = ({ core, services }) => async (expression, defaultImage) => {

    const { name: defaultName, email, roleName } = core.tags.parseEmailExpression(expression);
    const { name, imageBlob } = await services.gravatar.fetchNameAndImage(email || defaultName, defaultName, defaultImage);
    const tagData = { name, roleName, imageBlob };
    return services.tags.insertTagAsync(tagData);

};
