module.exports = ({ el, optionsBar, elements, subscriptions, config }) => () => {
    
    const $layout = elements.layout({
        layout: config.options.layout, 
        components: optionsBar.options
    });

    const $optionsBar = el('div').append($layout);
    $optionsBar.className = 'options-bar visible-false';

    const $container = el('div', 'options-bar-container').append($optionsBar);

    subscriptions.tags.onFirstInsert(() => {
        $optionsBar.className = 'options-bar visible-true';
    });

    return $container;
    
};
