const upperFirst = require('lodash/upperFirst');

module.exports = ({ el, elements, services, subscriptions, config }) => optionName => {

    const { min, max, step } = config.options[optionName];
    const isEmpty = () => $number.value === '';

    const onChange = cancel => () => {
        if (cancel()) return;
        const oldVal = services.settings.getOption(optionName);
        const val = isEmpty() ? oldVal : parseInt($number.value);
        const newVal = val < min ? min : val > max ? max : val;
        $number.value = newVal;
        services.settings.changeOption(optionName, newVal);
    };

    const $number = el('input', { type: 'number', className: optionName, min, max, step })
        .addEventListener('input', onChange(() => isEmpty()))
        .addEventListener('blur', onChange(() => false));
    
    subscriptions.settings.options.onChange(optionName, optionValue => {
        $number.value = optionValue;
    }).invoke();

    const labelText = upperFirst(optionName);
    return elements.label(labelText, $number);

};

/* 
About <input type="number">
- Value can be any floating-point number, or empty.
- Floating-point numbers avoided by setting `step` to an integer.
- Automatically rejects non-numeric values (except empty).
- Provides validation, but does not actually reject values outside min and max range.
*/
