module.exports = ({ el, services, subscriptions }) => () => {

    const $errorMessage = el('div');
    
    const $dismiss = el('a', { textContent: 'Dismiss' })
        .addEventListener('click', services.gravatar.status.resume);

    const $error = el('div').append($errorMessage, $dismiss);
    
    subscriptions.settings.gravatar.onChange('status', () => {
        $error.className = `error-message visible-${services.gravatar.status.isError()}`;
    }).invoke();

    subscriptions.settings.gravatar.onChange('errorMessage', errorMessage => {
        $errorMessage.textContent = errorMessage;
    }).invoke();

    return $error;

};
