const compose = require('module-composer');
const merge = require('lodash/merge');
const lib = require('./lib');
const src = {
    config: require('./config'),
    io: require('./io')
};

module.exports = ({ window, ...args }) => {

    const config = merge({}, src.config, args.config);
    const io = merge({}, src.io({ window, config }), args.io);

    const state = {};
    const storage = compose(lib.storage, { config });
    const { stores, settings, subscriptions } = storage.initialise(state);

    const core = compose(lib.core, { config });
    const services = compose(lib.services, { subscriptions, stores, settings, core, io, config });
    
    const elements = compose(lib.elements, { window });
    const { el } = elements;
    const components = compose(lib.components, { el, elements, services, subscriptions, config });
    
    services.system.initialise();
    
    const context = {
        components,
        elements,
        services,
        storage,
        subscriptions,
        settings,
        stores,
        state,
        core,
        io,
        config
    };

    return { context, ...context };
};
